import Vue from 'vue';
import VueRouter from 'vue-router';
//官方文件

//import Home from '@/components/HelloWorld';
import page from '@/components/pages/page';
import child1 from '@/components/pages/child';
import child2 from '@/components/pages/child2';
import child3 from '@/components/pages/child3';
import Menu from '@/components/pages/menu';

import Login from '@/components/pages/login';
import Dashboard from '@/components/dashboard';
import Product from '@/components/pages/product';
import Orders from '@/components/pages/Orders';
import Coupons from '@/components/pages/Coupons';

import CustomerOrder from '@/components/pages/CustomerOrders'
import CustomerCheckout from '@/components/pages/CustomerCheckout'
Vue.use(VueRouter)
// import { createRouter, createWebHistory } from 'vue-router'
import jsHomePage from '@/jsHW/jsHomePage'


export default new VueRouter({
    // mode:'history',
    linkActiveClass:'box', //class標籤替換
    routes:[
        {
            path:'*',
            redirect:'/login'
        },
        // { 
        //     name:'首頁',//元件呈現名稱
        //     path:'/',//對應虛擬路徑
        //     component:Home,//對應元件
        //     meta: { requiresAuth: true },
        // },
        { 
            name:'dashboard',//元件呈現名稱
            path:'/admin',//對應虛擬路徑
            component:Dashboard,//對應元件
            children:[
                {
                    path:'products',
                    name:'products',
                    component:Product,
                    meta: { requiresAuth: true },
                },
                {
                    path:'orders',
                    name:'orders',
                    component:Orders,
                    meta: { requiresAuth: true },
                },
                {
                    path:'coupons',
                    name:'Coupons',
                    component:Coupons,
                    meta: { requiresAuth: true },
                },
            ],
        },
        {
            name:"Login",
            path:'/login',
            component:Login,
        },
        {
            //name:'分頁',//元件呈現名稱。  因為預設會直接載入childone,childone的name會取代page的name
            path:'/page',//新路徑
            //component:page,//新元件
            components:{
                default: page,
                menu: Menu,
            },
            children:[
                {
                    name:'childone',
                    path:'',  //預設會載入child one
                    component:child1,
                },
                {
                    name:'childtwo',
                    path:'child2',
                    component:child2,
                },
                {
                    name:'childthree',
                    path:'child/',
                    component:child3,
                },
            ]
        },
        { 
            name:'dashboard',//元件呈現名稱
            path:'/',//對應虛擬路徑
            component:Dashboard,//對應元件
            children:[
                {
                    path:'customer_order',
                    name:'CustomerOrder',
                    component:CustomerOrder,
                    // meta: { requiresAuth: true }, 需要驗證才加
                },
                {
                    path:'CustomerCheckout/:orderId',
                    name:'CustomerCheckout',
                    component:CustomerCheckout,
                    // meta: { requiresAuth: true }, 需要驗證才加
                }
            ],
        },
        {
            //name:'分頁',//元件呈現名稱。  因為預設會直接載入childone,childone的name會取代page的name
            path:'/jsHomeWork',//新路徑
            name:'js_HW',
            //component:page,//新元件
            components:{
                default: jsHomePage,
            },

        },
        
    ],

});


