<template>
<div>
    <loading :active.sync="isLoading"></loading>
    <div class="row mt-4">
    <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm">
        <div style="height: 150px; background-size: cover; background-position: center"
            :style="{backgroundImage: `url(${item.imageUrl})`}">
        </div>
        <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
            <h5 class="card-title">
            <a href="#" class="text-dark">{{ item.title }}</a>
            </h5>
            <p class="card-text">{{ item.content }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
            <div class="h5" v-if="!item.price">{{ item.origin_price }} 元</div>
            <del class="h6" v-if="item.price">原價 {{ item.origin_price }} 元</del>
            <div class="h5" v-if="item.price">現在只要 {{ item.price }} 元</div>
            </div>
        </div>
        <div class="card-footer d-flex">
            <button type="button" class="btn btn-outline-secondary btn-sm" @click.prevent="getProduct(item.id)">
            <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
            查看更多
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(product.id, product.num)">
            <i class="fas fa-spinner fa-spin"></i>
            加到購物車
            </button>
        </div>
        </div>
    </div>
    </div>
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <img :src="product.imageUrl" class="img-fluid" alt="">
                    <blockquote class="blockquote mt-3">
                    <p class="mb-0">{{ product.content }}</p>
                    <footer class="blockquote-footer text-right">{{ product.description }}</footer>
                    </blockquote>
                    <div class="d-flex justify-content-between align-items-baseline">
                    <div class="h4" v-if="!product.price">{{ product.origin_price }} 元</div>
                    <del class="h6" v-if="product.price">原價 {{ product.origin_price }} 元</del>
                    <div class="h4" v-if="product.price">現在只要 {{ product.price }} 元</div>
                    </div>
                    <select name="" class="form-control mt-3" v-model="product.num">
                        <option :value="num" v-for="num in 10" :key="num">
                            選購 {{num}} {{product.unit}}
                        </option>
                    </select>
                </div>
                <div class="modal-footer">
                    <div class="text-muted text-nowrap mr-3">
                    小計 <strong>{{ product.num * product.price }}</strong> 元
                    </div>
                    <button type="button" class="btn btn-primary"
                    @click="addtoCart(product.id, product.num)">
                    <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
                    加到購物車
                    </button>
                </div>
            </div>
        </div>
        </div>
        <div><h2 style="text-align:center">購物車列表</h2></div>
        <div class="my-5 row justify-content-center">
            <table class="table ">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">品名</th>
                        <th scope="col">數量</th>
                        <th scope="col">單價</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in cart.carts" :key="item.id" >
                        <td class="align-middle">
                            <button type="button" class="btn btn-outline-danger btn-sm" @click.prevent="removeCarItem(item.id)">
                                <i class="far fa-trash-alt"></i>
                            </button>
                        </td>
                        <td class="align-middle">{{item.product.title }}
                            <div class="text-success" v-if="item.coupon">
                                已套用優惠券
                            </div>
                        </td>
                        <td class="align-middle">{{ item.qty }}</td>
                        <td class="align-middle">{{ item.product.price }}</td>
                        
                        <!-- <td class="align-middle">{{ item.final_total }}</td> API回傳的物品售價  -->
                        
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                        <td colspan="2" ></td>
                        <td class="">總計</td>
                        <td >{{ cart.total }}</td>
                    </tr>
                    <tr v-if="cart.final_total !== cart.total">
                        <td colspan="2" ></td>
                        <td class="text-success">折扣價</td>
                        <td class="text-success">{{ cart.final_total }}</td>
                    </tr>
                </tfoot>
            </table>
            <div class="input-group mb-3 input-group-sm">
            <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
            <div class="input-group-append">
                <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">
                套用優惠碼
                </button>
            </div>
            </div>
        </div>
        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="createOrder">
                <div class="form-group">
                    <label for="useremail">Email</label>
                    <input type="email" class="form-control" name="email" id="useremail"
                        v-validate="'required|email'"
                        :class="{'is-invalid': errors.has('email')}"
                        v-model="form.user.email" placeholder="請輸入 Email">
                <span class="text-danger" v-if="errors.has('email')">
                    {{ errors.first('email') }}
                </span>
                </div>

                <div class="form-group">
                <label for="username">收件人姓名</label>
                <input type="text" class="form-control" name="name" id="username"
                    :class="{'is-invalid': errors.has('name')}"
                    v-model="form.user.name" v-validate="'required'" placeholder="輸入姓名">
                <span class="text-danger" v-if="errors.has('name')">姓名必須輸入</span>
                </div>

                <div class="form-group">
                <label for="usertel">收件人電話</label>
                <input type="tel" class="form-control" id="usertel"
                    v-model="form.user.tel" placeholder="請輸入電話">
                </div>

                <div class="form-group">
                <label for="useraddress">收件人地址</label>
                <input type="address" class="form-control" name="address" :class="{'is-invalid': errors.has('address')}"
                id="useraddress" v-model="form.user.address" v-validate="'required'"
                    placeholder="請輸入地址">
                <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
                </div>

                <div class="form-group">
                <label for="useraddress">留言</label>
                <textarea name="" id="" class="form-control" cols="30" rows="10"
                    v-model="form.message"></textarea>
                </div>
                <div class="text-right">
                <button class="btn btn-danger">送出訂單</button>
                </div>
            </form>
            </div>


    </div>
</template>

<script>
import $ from 'jquery'
export default {
    data() {
        return {
            coupon_code: '',
            products: [],
            cart: {},
            isLoading: false,
            status:{
                loadingItem:'',
            },
            product:{},
            form:{
                user:{ 
                    name:'',
                    email:'',
                    tel:'',
                    address:'',
                },
                message:'',
            },
        };
    },
    methods: {
        getProducts() {
            const vm = this;
            console.log("process:",process)

            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMER_PATH}/products`;
            vm.isLoading = true;
            this.$http.get(url).then((response) => {
            vm.products = response.data.products;
            console.log(response.data);
            vm.isLoading = false;
            });
        },
        getProduct(id) {
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMER_PATH}/product/${id}`;
            vm.status.loadingItem = id;
            this.$http.get(url).then((response) => {
            vm.product = response.data.product;
            //   vm.product.price = ''
            $('#productModal').modal('show')
            console.log(response);
            vm.status.loadingItem = '';
            });
        },
        addtoCart(id,qty = 1){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMER_PATH}/cart`;
            vm.status.loadingItem = id;
            const cart = {
                product_id:id,
                qty
            }
            this.$http.post(url,{data:cart}).then((response) => {
                //vm.product = response.data.product;
                console.log("cart response:",response.data);
                vm.status.loadingItem = '';
                vm.getCart();
                $('#productModal').modal('hide');
            });
        },
        getCart(){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMER_PATH}/cart`;
            vm.isLoading = true;
            this.$http.get(url).then((response) => {
                vm.cart = response.data.data;
                //console.log("vm.cart:",vm.cart)
                vm.isLoading = false;
            });
        },
        removeCarItem(id){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMER_PATH}/cart/${id}`;
            vm.isLoading = true;
            this.$http.delete(url).then((response) => {
            // console.log(response.data)
            vm.isLoading = false;
            vm.getCart();
            });

        },
        addCouponCode(){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMER_PATH}/coupon`;
            const coupon = {
                code: vm.coupon_code,
            };
            console.log("coupon:",coupon)
            vm.isLoading = true;
            this.$http.post(url, { data: coupon }).then((response) => {
                console.log(response);
                vm.getCart();
                vm.isLoading = false;
            });
        },
        createOrder(){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMER_PATH}/order`;
            const order = vm.form
            // vm.isLoading = true;
            this.$validator.validate().then((result) =>{
                if(result){
                    this.$http.post(url, { data: order }).then((response) => {
                        console.log('訂單已建立',response.data);
                        if(response.data.success){
                            vm.$router.push(`/CustomerCheckout/${response.data.orderId}`)
                        }
                        //
                        // vm.getCart();
                        vm.isLoading = false;
                    });
                }
                else{
                    console.log("欄位填寫不正確")
                }
            });

        }
    },
    created() {
        this.getProducts();
        this.getCart();
    },
};
</script>


